body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(counting\ tool.jpg);
  background-position: center bottom;
  background-size: cover;
}

.container {
  text-align: center;
  background-color: rgba(86, 86, 86, 0.3);
  padding: 100px 100px;
  border-radius: 7px;
}

h1 {
  font-size: 75px;
  color: black;
}

.btns {
  display: inline-block;
}

.btn1 , .btn2 {
  width: 150px;
  height: 50px;
  font-size: 18px;
  border-radius: 7px;
  outline: none;
  background-color: transparent;
  border: 2px solid black;
  color: black;
  font-weight: 600;
}

.btn1 {
width: 150px;
  height: 30px;
  border-radius: 7px;
  outline: none;
  margin-left: 25px;
  background-color: white;
  border: 2px solid black;
  color: black;
  font-weight: 600;}

.btn2 {
  width: 150px;
  height: 30px;
  border-radius: 7px;
  outline: none;
  margin-left: 25px;
  background-color: white;
  border: 2px solid black;
  color:black;
  font-weight: 600;
}

.form-container {
  margin-top: 30px;
}

.input {
  width: 250px;
  border-top: solid yellow;
  border-left: solid yellow;
  border-right: solid yellow;
  border-bottom: 2px solid black;
  outline: yellow;
  font-size: 18px;
  color: black;
  background-color: black;
  text-align: center;
}

::placeholder {
  color: black;
  font-size: 14px;
  text-transform: capitalize;
  text-align: center;
}